import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { ExtensionMethod } from "../ExtensionMethod";
import { Plane } from "../omgdi/Plane";
import { PalProcessAttribute } from "../mex/PalProcessAttribute";

export class BPMNPlane extends Plane {
    constructor(model: BpmnModel) {
        super(model);
    }

    readonly ClrTypeID: string = "bpmn.bpmndi.BPMNPlane";
    NewInstance(): BPMNPlane {
        return new BPMNPlane(this.Model);
    }

    BpmnElementId: string;

    static readonly MEXTAG = "plane.bpa"
    // bpaAttribute: string;
    palProcessAttribute: PalProcessAttribute;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.BpmnElementId = null;
        this.palProcessAttribute = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "BpmnElementId", this.BpmnElementId);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.BpmnElementId = obj.BpmnElementId;
    }

    LoadExtensionElements(extensionElementsObj: {}): void {
        super.LoadExtensionElements(extensionElementsObj);

        const palProcessAttributeObj = ExtensionMethod.GetAttributeValue(extensionElementsObj, "palProcessAttribute");
        if (palProcessAttributeObj != null) {
            this.palProcessAttribute = new PalProcessAttribute(this.Model);
            this.palProcessAttribute.LoadFromJsonObject(palProcessAttributeObj);
        }
    }

    WriteExtensionElements(extensionElementsObj: {}): void {
        super.WriteExtensionElements(extensionElementsObj);

        if (this.palProcessAttribute != null) {
            ExtensionMethod.SetAttributeValue(extensionElementsObj, "palProcessAttribute", this.palProcessAttribute.ConvertToJsonObject());
        }
    }

}